<template>
  <hc-global-header hcTitleHref="..">
    <template slot="header-title">
     ACME Stock Co
    </template>
    <template slot="header-links">
      <hc-global-header-item hc-href="">
        <hc-icon href="hc(user)"></hc-icon> Richard's Account
      </hc-global-header-item>
      <hc-global-header-item hc-href="">
        <hc-icon href="hc(manage)"></hc-icon> Settings
      </hc-global-header-item>
      <hc-global-header-item hc-href="">
        <hc-icon href="hc(help)"></hc-icon> Help
      </hc-global-header-item>
    </template>
    <template slot="header-nav-links">
      <hc-global-header-nav-item hc-href="">
        <hc-icon class="icon icon--caret" href="hc(caret)"></hc-icon>
        Home
      </hc-global-header-nav-item>
    </template>
  </hc-global-header>
</template>

<script>
  import hcIcon from '../../node_modules/@hybrid-cloud/cirrus-vue/src/components/hc-icon/hc-icon'
  import hcGlobalHeader from '../../node_modules/@hybrid-cloud/cirrus-vue/src/components/hc-global-header/hc-global-header'
  import hcGlobalHeaderItem from '../../node_modules/@hybrid-cloud/cirrus-vue/src/components/hc-global-header/hc-global-header-item'
  import hcGlobalHeaderNavItem from '../../node_modules/@hybrid-cloud/cirrus-vue/src/components/hc-global-header/hc-global-header-nav-item'

  export default {
    name: 'stockHeader',
    components: {
      'hc-global-header': hcGlobalHeader,
      'hc-global-header-item': hcGlobalHeaderItem,
      'hc-global-header-nav-item': hcGlobalHeaderNavItem,
      'hc-icon': hcIcon
    }
  }
</script>

<style type="scss">
</style>



