<template>
  <div class="modal" :data-modal="hcName" role="dialog" :aria-labelledby="`modal-title-${hcId}`"
       :aria-describedby="`modal-desc-${hcId}`">
    <div class="overlay js-close-modal"></div>
    <div class="modal-card">
      <hc-button hc-style="flat" class="modal-card__close js-close-modal">
        <hc-icon href="hc(close-modal)" class="icon--close-modal"></hc-icon>
      </hc-button>
      <h4 :id="`modal-title-${hcId}`" class="h2 modal-card__heading">{{ hcTitle }}</h4>
      <p :id="`modal-desc-${hcId}`" class="modal-card__content">
        Are you sure you want to delete this portfolio?
      </p>
      <hc-button hcStyle="primary" class="js-modal-focus js-close-modal" v-on:click="performAction">Ok</hc-button>
      <hc-button class="js-close-modal" hcStyle="flat">Cancel</hc-button>
    </div>
  </div>
</template>

<script>
  import hcIds from '../../../node_modules/@hybrid-cloud/cirrus-vue/src/mixins/hc-ids'
  import hcButton from '../../../node_modules/@hybrid-cloud/cirrus-vue/src/components/hc-button/hc-button'

  // MSG Bus to send new portfolio data back to the table.
  import bus from '../bus'

  // REST project
  import axios from 'axios'

  export default {
    name: 'delete-portfolio-modal',
    mixins: [hcIds],
    components: {
      hcButton
    },
    props: {
      owner: {
        type: String,
        required: true
      },
      hcName: {
        type: String,
        required: true
      },
      hcTitle: {
        type: String,
        required: true
      }
    },
    methods: {
      performAction () {
        var url
        var body
        url = process.env.API_ENDPOINT + '/trader/summary'
        console.log('Here is the owner to delete: ' + this.owner)
        body = {
          owner: this.owner,
          action: 'delete'
        }
        var formatter = new Intl.NumberFormat('en-US', {
          style: 'currency',
          currency: 'USD',
          minimumFractionDigits: 2
          // the default value for minimumFractionDigits depends on the currency
          // and is usually already 2
        })
        axios.post(url, body)
          .then(response => {
            console.log('Here is the response')
            console.log(response)
            for (var i in response.data) {
              console.log(response.data[i])
              response.data[i].total = formatter.format(response.data[i].total)
              this.updatedPortfolio.push(response.data[i])
              bus.$emit('updatedPortfolio', this.updatedPortfolio)
            }
          })
          .catch(e => {
            this.errors.push(e)
          })
      }
    },
    data () {
      return {
        updatedPortfolio: []
      }
    }
  }
</script>

<style lang="scss">
</style>



