<template>
  <div>
    <stock-header></stock-header>
    <div class="container">
      <div class="row u-push-top--large">
        <hc-heading hc-rank="1">Dashboard</hc-heading>
      </div>
      <div class="row u-push-top--large">
          <div class="box box--center">
            <djia></djia>
          </div>
      </div>
      <div class="row u-push-top--large">
        <div class="box box--2-3rd">
          <hc-card>
            <hc-card-body>
              <portfolios></portfolios>
            </hc-card-body>
          </hc-card>
        </div>
        <div class="box box--3rd">
          <hc-card>
            <hc-card-body>
              <hc-card-title class="headerDivider" hc-rank="1" hc-style-level="5">
                Recent Activity
              </hc-card-title>
              <hc-card-body class="u-text--reg">
                Some Event <br/>
                Another Event
              </hc-card-body>
            </hc-card-body>
          </hc-card>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
  import stockHeader from './Header'
  import portfolio from './portfolio/Portfolios'
  import DJIA from './portfolio/DJIA'

  import hcHeadings from '../../node_modules/@hybrid-cloud/cirrus-vue/src/components/hc-heading/hc-heading'
  import hcCards from '../../node_modules/@hybrid-cloud/cirrus-vue/src/components/hc-card/index.js'

  export default {
    name: 'hello',
    components:
      Object.assign({
        'stockHeader': stockHeader,
        'djia': DJIA,
        'portfolios': portfolio,
        'hc-heading': hcHeadings
      }, hcCards.components),
    updated () {
      import('../../node_modules/@hybrid-cloud/cirrus/dist/js/cirrus.es5.js')
    },
    data () {
      return {
        msg: 'Welcome to Your Vue.js App'
      }
    }
  }
</script>

<style lang="scss">
  @import '../../node_modules/@hybrid-cloud/cirrus-vue/src/globals/scss/cirrus-globals';
  .headerDivider {
    padding: 10px;
    border-color: black;
    border-width: 3px;
    border-bottom-style: solid;
  }
</style>



