<template>
  <span>
    <tr v-for="row, rowIndex in portfolioData.stocks" :row-index="rowIndex" class="table__row" data-table-row="" :key="row">
      <td class="table__cell">
        <span>{{ row.symbol }}</span>
      </td>
      <td class="table__cell">
        <span>{{ row.shares }}</span>
      </td>
      <td class="table__cell">
        <span>{{ row.price }}</span>
      </td>
      <td class="table__cell">
        <span>{{ row.date }}</span>
      </td>
      <td class="table__cell">
        <span>{{ row.total }}</span>
      </td>
    </tr>
  </span>
</template>

<script>
  import hcModal from '../../../node_modules/@hybrid-cloud/cirrus-vue/src/components/hc-modal/hc-modal'

  export default {
    name: 'stocks',
    components: {
      'hc-modal': hcModal
    },
    props: {
      portfolioData: Object
    }
  }
</script>



