<template>
  <div>
    <stock-header></stock-header>
    <div class="container">
      <div class="row u-push-top--large">
        <div class="box box--full">
        <hc-page-header
          hc-breadcrumb-text="Previous"
          :hc-title="this.user"></hc-page-header>
        </div>
      </div>
      <div class="row u-push-top--large">
        <div class="box box--2-3rd">
          <hc-card>
            <hc-card-title class="headerDivider cardHeader" hc-rank="1" hc-style-level="5">
              Client Overview
            </hc-card-title>
            <hc-card-body>
              <person-info :portfolioValue="this.totalValue"></person-info>
              <br/>
              <personTable :caption="caption" :headings="headings" :data="data" v-on:sortColumn="sortColumn"></personTable>
            </hc-card-body>
          </hc-card>
        </div>
        <div class="box box--1-3rd">
          <hc-card>
            <hc-card-body>
              <personTable :caption="caption" :headings="headings" :data="data" v-on:sortColumn="sortColumn"></personTable>
            </hc-card-body>
          </hc-card>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
  import hcPageHeader from '../../../node_modules/@hybrid-cloud/cirrus-vue/src/components/hc-page-header/hc-page-header'
  import hcCards from '../../../node_modules/@hybrid-cloud/cirrus-vue/src/components/hc-card/index.js'
  // Build on top of cirrus-vue
  import personTable from './PersonTable'
  import stockHeader from '../Header'
  import personInfo from './Personinfo'

  // Import msg bus
  import bus from '../bus'

  // REST project
  import axios from 'axios'

  // Get cirrus lib

  export default {
    name: 'person',
    components:
      Object.assign({
        'hc-page-header': hcPageHeader,
        'stockHeader': stockHeader,
        'personTable': personTable,
        'personInfo': personInfo
      }, hcCards.components),
    updated () {
      import('../../../node_modules/@hybrid-cloud/cirrus/dist/js/cirrus.es5.js')
    },
    created () {
      bus.$on('updatedPortfolio', this.updateTable)
    },
    mounted () {
      this.totalValue = this.$route.params.totalValue
      this.user = this.$route.params.user
      console.log(this.user)
      this.getPortfolio(this.user)
    },
    methods: {
      getPortfolio (user) {
        var formatter = new Intl.NumberFormat('en-US', {
          style: 'currency',
          currency: 'USD',
          minimumFractionDigits: 2
          // the default value for minimumFractionDigits depends on the currency
          // and is usually already 2
        })
        console.log('user is ' + user)
        axios.get(process.env.API_ENDPOINT + '/trader/viewPortfolio?owner=' + user)
          .then(response => {
            console.log('here comes the response from viewPortfolio')
            var person = response.data
            console.log(person)
            // Format total cost
            person.total = formatter.format(person.total)
            for (var i in person.stocks) {
              person.stocks[i].price = formatter.format(person.stocks[i].price)
              person.stocks[i].total = formatter.format(person.stocks[i].total)
            }
            console.log(person)
            this.data = person
          })
          .catch(e => {
            this.errors.push(e)
          })
      },
      updateTable (profileData) {
        console.log(this.owner)
        console.log(this.data)
        if (profileData) {
          this.data = []
          for (var i in profileData) {
            console.log(profileData[i])
            this.data.push(profileData[i])
            // util.initOnReady(modal.init)
          }
        } else {
          this.getPortfolio(this.user)
        }
      },
      sortColumn (colIndex) {
        // TODO Broken
        // NOTE: A sample sort, not a sensible one. Columns like status and date will likely need specific sorts

        let sortFn

        console.log(colIndex)
        console.log(this.headings[colIndex].sortedReverse)
        if (this.headings[colIndex].sortedReverse) {
          this.headings[colIndex].sortedReverse = false
          sortFn = (a, b) => {
            console.log('sort normal')
            console.log(b)
            console.log(a)
            console.log(a[this.headings[colIndex].field])
            console.log(b[this.headings[colIndex].field])
            a[this.headings[colIndex].field].localeCompare(b[this.headings[colIndex].field])
          }
        } else {
          // may not exist, use this.$set
          this.$set(this.headings[colIndex], 'sortedReverse', true)
          console.log('sort reverse')
          sortFn = (a, b) => {
            console.log(b)
            console.log(a)
            console.log(a[this.headings[colIndex].field])
            console.log(b[this.headings[colIndex].field])
            b[this.headings[colIndex].field].localeCompare(a[this.headings[colIndex].field])
            var options = {
              options: {
                ignorePunctuation: true
              }
            }
            console.log(b[this.headings[colIndex].field].localeCompare(a[this.headings[colIndex].field], options))
          }
          this.data.sort(sortFn)
        }
      }
    },
    data () {
      return {
        errors: [],
        caption: `<span class="u-text--bold">Clients</span>`,
        headings: [
          {
            label: 'Stock Symbol',
            hideLabel: false,
            sortable: true,
            field: 'owner'
          }, {
            label: 'Shares',
            sortable: true,
            field: 'total'
          }, {
            label: 'Price',
            sortable: true,
            sortedReverse: true,
            field: 'loyalty'
          }, {
            label: 'Date Quoted',
            sortable: false
          }, {
            label: 'Total',
            sortable: false
          }
        ],
        user: null,
        totalValue: null,
        data: []
      }
    }
  }
</script>

<style lang="scss">
  @import '../../../node_modules/@hybrid-cloud/cirrus-vue/src/globals/scss/cirrus-globals';
  .headerDivider {
    padding: 10px;
    border-color: black;
    border-width: 3px;
    border-bottom-style: solid;
  }
  .cardHeader {
    text-align: left !important;
    margin: 15px 0 5px;
  }
</style>



