<template>
  <div class="row">
    <div class="box box--3rd">
      <hc-heading hc-rank="1" hc-style-level="5">This Guy's Name</hc-heading>
      <ul>
        <!-- TODO We don't store the address -->
        <li>1111 Some St</li>
        <li>Any Town, USA 00000</li>
      </ul>
    </div>
    <div class="box box--3rd u-text--centered">
      <hc-heading hc-rank="1" hc-style-level="6">PORTFOLIO VALUE</hc-heading>
      <hc-heading class="eyeCatcher u-text--centered u-text--light" hc-rank="3" hc-style-level="1">{{ this.portfolioValue }}</hc-heading>
    </div>
    <div class="box box--3rd u-text--centered">
      <hc-heading hc-rank="1" hc-style-level="6">YEAR TO DATE</hc-heading>
      <!-- TODO We don't track performance of stocks -->
      <hc-heading class="eyeCatcher u-text--centered u-text--light stockIncrease" hc-rank="3" hc-style-level="1">+9.34%</hc-heading>
    </div>
  </div>
</template>

<script>
  import hcIcon from '../../../node_modules/@hybrid-cloud/cirrus-vue/src/components/hc-icon/hc-icon'
  import hcHeadings from '../../../node_modules/@hybrid-cloud/cirrus-vue/src/components/hc-heading/hc-heading'

  export default {
    name: 'personInfo',
    components: {
      'hc-heading': hcHeadings,
      'hc-icon': hcIcon
    },
    props: {
      portfolioValue: String
    }
  }
</script>

<style scoped lang="scss">
  // @import '../../../node_modules/@hybrid-cloud/cirrus-vue/src/globals/scss/cirrus-globals';
  // Customized Styles
  @import '../../styles/styles.scss';
</style>



