<!--

Cirrus style input select field.
Label: Transcluded

Attributes:
  hc-caption: string or html
  hc-headings: array of
    - heading
      - label - string - used in cells
      - hideLabel - true or false
      - sortable - true or false
  hc-data: array of rows, containing array of cell data which contain strings or html
    - row
      - cell: string or html
    - row
      - cell
    - row
      .
      .
      .

Events:
  sortColumn: columnIndex
-->
<template>
  <div>
    <table class="table">
      <caption v-if="caption" class="table-header">
        <span class="u-text--bold">Stock Portfolio</span>
      </caption>
      <thead v-if="headings" class="table__head">
      <tr class="table__row">
        <th v-for="heading, columnIndex in headings" class="table__cell" scope="col" :key="heading"
            v-on:click="sortColumn (columnIndex)">
          <span v-if="heading.label && !heading.hideLabel" v-text="heading.label"></span>
          <hc-icon v-if="heading.sortable" class="icon"
                   :class="{ 'icon--down': !heading.sortedReverse, 'icon--up': heading.sortedReverse }"
                   :href="heading.sortedReverse ? 'hc (arrow-up)' : 'hc (arrow-down)'"></hc-icon>
        </th>
      </tr>
      </thead>
      <tfoot>
        <tr>
          <div class="row"><br/></div>
          <div class="row u-float--right box--vertical-center">
            <div class="box box--full">
              <hc-button hcStyle="secondary" disabled="true">Sell Stock</hc-button>
              <hc-button hcModalPop="true" hcModalTarget="Buy" hcStyle="primary">Buy Stock</hc-button>
            </div>
          </div>
        </tr>
      </tfoot>
      <tbody class="table__body">
      <stocks v-bind:portfolioData=data></stocks>
      </tbody>
    </table>
    <action-modal action="buy" :portfolio="data" hcName="Buy" :hcTitle="'Buy stocks for '+ data.owner">
    </action-modal>
  </div>
</template>

<script>
  import hcIcon from '../../../node_modules/@hybrid-cloud/cirrus-vue/src/components/hc-icon/hc-icon'
  import hcButton from '../../../node_modules/@hybrid-cloud/cirrus-vue/src/components/hc-button/hc-button'
  import stocks from './Stocks'
  import actionModal from '../portfolio/ActionModal'

  export default {
    name: 'personTable',
    components: {
      'hc-icon': hcIcon,
      'hc-button': hcButton,
      'stocks': stocks,
      'action-modal': actionModal
    },
    methods: {
      sortColumn (columnIndex) {
        console.log(columnIndex)
        this.$emit('sortColumn', columnIndex)
      }
    },
    updated () {
      import('../../../node_modules/@hybrid-cloud/cirrus/dist/js/cirrus.es5.js')
    },
    props: {
      caption: String,
      headings: Array,
      data: Object
    },
    data () {
      return {
        user: null
      }
    }
  }
</script>
<style lang="scss">
  @import '../../../node_modules/@hybrid-cloud/cirrus-vue/src/globals/scss/cirrus-globals';
  @import '../../../node_modules/@hybrid-cloud/cirrus/src/scss/elements/tables/tables';
</style>



