<template>
  <span>
    <tr v-for="row, rowIndex in portfolioData" :row-index="rowIndex" class="table__row" data-table-row="" :key="row">
      <td class="table__cell">
        <router-link :to="{ name: 'Person', params: { user: row.owner, totalValue: row.total }}">
          {{ row.owner }}
        </router-link>
      </td>
      <td class="table__cell">
        <span>{{ row.total }}</span>
      </td>
      <td class="table__cell">
        <span>{{ row.loyalty }}</span>
      </td>
      <td class="table__cell">
        <span>
          <hc-overflow class="overflow-sample" hc-placeholder="Select an option...">
            <portfolio-overflow-item hcModalPop="true"
                                     :hcModalTarget="'Buy ' + rowIndex">Buy Stocks</portfolio-overflow-item>
            <portfolio-overflow-item hcModalPop="true"
                                     :hcModalTarget="'Sell ' + rowIndex">Sell Stocks</portfolio-overflow-item>
            <portfolio-overflow-item hcHasDivider="true" hcModalPop="true" :hcModalTarget="'Delete ' + rowIndex">Delete Portfolio</portfolio-overflow-item>
          </hc-overflow>
        </span>
      </td>

      <!--
      <hc-modal :hcName="'Buy ' + rowIndex" :hc-title="'Buy stocks for '+ row.owner">
        <hc-text v-model="stockName" name="buyStock" label="Stock Name">IBM</hc-text>
        <hc-text v-model="sharesNumber" name="buyShares" label="Shares"></hc-text>
      </hc-modal>
      -->
      <action-modal action="buy" :portfolio="row" :hcName="'Buy ' + rowIndex" :hcTitle="'Buy stocks for '+ row.owner">
      </action-modal>
      <action-modal action="sell" :portfolio="row" :hcName="'Sell ' + rowIndex" :hc-title="'Sell stocks for '+ row.owner">
      </action-modal>
      <delete-portfolio-modal :owner="row.owner" :hcName="'Delete ' + rowIndex" :hcTitle="'Delete '+ row.owner + '\'s Portfolio'"></delete-portfolio-modal>
    </tr>
  </span>
</template>

<script>
  import portfolioOverflowItem from './MenuModal'
  import actionModal from './ActionModal'
  import deletePortfolioModal from './DeletePortfolioModal'
  import hcOverflow from '../../../node_modules/@hybrid-cloud/cirrus-vue/src/components/hc-overflow/hc-overflow'
  import hcModal from '../../../node_modules/@hybrid-cloud/cirrus-vue/src/components/hc-modal/hc-modal'

  export default {
    name: 'portfolioPerson',
    components: {
      'portfolio-overflow-item': portfolioOverflowItem,
      'action-modal': actionModal,
      'delete-portfolio-modal': deletePortfolioModal,
      'hc-overflow': hcOverflow,
      'hc-modal': hcModal
    },
    props: {
      portfolioData: Array
    }
  }
</script>



