<template>
  <div>
    <div class="row">
      <div class="box box--full">
        <portfolioTable :caption="caption" :headings="headings" :data="data" v-on:sortColumn="sortColumn"></portfolioTable>
      </div>
    </div>
  </div>
</template>

<script>
  // cirrus-vue
  import hcOverflow from '../../../node_modules/@hybrid-cloud/cirrus-vue/src/components/hc-overflow/hc-overflow'
  import hcModal from '../../../node_modules/@hybrid-cloud/cirrus-vue/src/components/hc-modal/hc-modal'

  // Build on top of cirrus-vue
  import stockHeader from '../Header'
  import portfolioTable from './PortfolioTable'
  import portfolioOverflowItem from './MenuModal'

  // Import msg bus
  import bus from '../bus'

  // REST project
  import axios from 'axios'

  // Get cirrus lib
  // import cirrus from '../../node_modules/@hybrid-cloud/cirrus/dist/js/cirrus.es5.js'

  export default {
    name: 'portfolios',
    components: {
      'stockHeader': stockHeader,
      'portfolioTable': portfolioTable,
      'portfolio-overflow-item': portfolioOverflowItem,
      'hc-overflow': hcOverflow,
      'hc-modal': hcModal
    },
    updated () {
      import('../../../node_modules/@hybrid-cloud/cirrus/dist/js/cirrus.es5.js')
    },
    created () {
      bus.$on('updatedPortfolio', this.updateTable)
    },
    mounted () {
      this.updateTable()
    },
    methods: {
      updateTable (profileData) {
        console.log(profileData)
        console.log(this.data)
        var formatter = new Intl.NumberFormat('en-US', {
          style: 'currency',
          currency: 'USD',
          minimumFractionDigits: 2
          // the default value for minimumFractionDigits depends on the currency
          // and is usually already 2
        })
        if (profileData) {
          this.data = []
          for (var i in profileData) {
            console.log(profileData[i])
            this.data.push(profileData[i])
            // util.initOnReady(modal.init)
          }
        } else {
          axios.get(process.env.API_ENDPOINT + '/trader/summary')
            .then(response => {
              for (var i in response.data) {
                console.log(response.data[i])
                response.data[i].total = formatter.format(response.data[i].total)
                this.data.push(response.data[i])
              }
              console.log(response.data)
            })
            .catch(e => {
              this.errors.push(e)
            })
        }
      },
      sortColumn (colIndex) {
        // TODO Broken
        // NOTE: A sample sort, not a sensible one. Columns like status and date will likely need specific sorts

        let sortFn

        console.log(colIndex)
        console.log(this.headings[colIndex].sortedReverse)
        if (this.headings[colIndex].sortedReverse) {
          this.headings[colIndex].sortedReverse = false
          sortFn = (a, b) => {
            console.log('sort normal')
            console.log(b)
            console.log(a)
            console.log(a[this.headings[colIndex].field])
            console.log(b[this.headings[colIndex].field])
            a[this.headings[colIndex].field].localeCompare(b[this.headings[colIndex].field])
          }
        } else {
          // may not exist, use this.$set
          this.$set(this.headings[colIndex], 'sortedReverse', true)
          console.log('sort reverse')
          sortFn = (a, b) => {
            console.log(b)
            console.log(a)
            console.log(a[this.headings[colIndex].field])
            console.log(b[this.headings[colIndex].field])
            b[this.headings[colIndex].field].localeCompare(a[this.headings[colIndex].field])
            var options = {
              options: {
                ignorePunctuation: true
              }
            }
            console.log(b[this.headings[colIndex].field].localeCompare(a[this.headings[colIndex].field], options))
          }
          this.data.sort(sortFn)
        }
      }
    },
    data () {
      return {
        errors: [],
        caption: `<span class="u-text--bold">Clients</span>`,
        headings: [
          {
            label: 'Name',
            hideLabel: false,
            sortable: true,
            field: 'owner'
          }, {
            label: 'Total Assets',
            sortable: true,
            field: 'total'
          }, {
            label: 'Risk Tolerance',
            sortable: true,
            sortedReverse: true,
            field: 'loyalty'
          }, {
            label: 'Action',
            sortable: false
          }
        ],
        data: []
      }
    }
  }
</script>

<style>

</style>



