<!--
  Cirrus style overflow item.
  Content: Transcluded

  Attributes:
    hc-has-divider: true or false

  Events:
    @click="$emit('click', $event)"

-->

<template>
  <li class="list-overflow__item" :class="{ 'with-divider': hcHasDivider }"
      role="presentation">
    <button :class="{'list-overflow__action': true, 'js-modal': hcModalPop}"
            :data-modal-target="hcModalTarget"
            role="menuitem"
            @click="$emit('click', $event)"
    >
      <slot></slot>
    </button>
  </li>
</template>

<script>
  import hcIds from '../../../node_modules/@hybrid-cloud/cirrus-vue/src/mixins/hc-ids'

  export default {
    name: 'portfolio-overflow-item',
    mixins: [hcIds],
    props: {
      hcHasDivider: { type: String, required: false },
      hcModalPop: { type: String, required: false },
      hcModalTarget: { type: String, required: false }
    }
  }
</script>

<style lang="scss">
  @import '../../../node_modules/@hybrid-cloud/cirrus-vue/src/globals/scss/cirrus-globals';
  @import '../../../node_modules/@hybrid-cloud/cirrus/src/scss/elements/tables/tables';
</style>



